.form{
 border-radius: 8px;
 padding: 30px;
 margin-bottom: 20px;
 box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
 display: flex;
 flex-direction: column;
 align-items: center;
}
.label{
   display: flex;
   justify-content:space-between;
   align-items: center;
   font-size: 20px;
   padding: 10px;
   width: 380px;
}
.inputName{
    
    margin-right:15px;
}
.btn {
    background-color: #f0f8ff;
    padding: 10px 20px 10px 20px;
    border-radius: 8px;
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    font-family: 'Ibarra Real Nova', serif;
    font-size: 20px;
    border: none;
    display: block;
    margin-top: 20px;
  }
  .btn:active {
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
  
  .btn:hover {
    background-color: #191970;
    color: #fff;
  }
  
  .input{

  border: 1px solid #87cefa;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  display: block;
  height: 30px;
  width: 250px;
 border-radius: 20px;
 background-color: 	#F0F8FF;
 outline: none;
 padding-left:20px;
 padding-right: 20px;
 font-size: 14px;

  }
